import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from "components/Header";
import Footer from "components/Footer";
import FooterBottom from "components/Footer/FooterBottom";
import iconLocation from 'assets/images/icons/icon-location.png';
import deleteIcon from 'assets/images/icons/Delete-icon.png';
import arrowLeft from 'assets/images/icons/arrow-left.png';
import './event.css';

class Cart extends Component {
  render() {
    return (
      <React.Fragment>
        <Header />
        <CartComp />
        <Footer />
        <FooterBottom />
      </React.Fragment>
    );
  }
}

export default Cart;

const CartComp = () => {
  return (
    <div className="main-wrap">

      <nav aria-label="Page breadcrumb" className="breadcrumb-nav">
        <div className="container">
          <ol className="breadcrumb">
            <li className="breadcrumb-item active" aria-current="page">
              <Link>Home</Link>
            </li>
            <li className="breadcrumb-item">Events</li>
          </ol>
        </div>
      </nav>

      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="page-top">
              <div className="page-head">
                <h2>CART</h2>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="bgWhiteShadow w-100">
          <div className="cartwrap">
            <div className="row">
              <div className="col-md-6">
                <div className="event-cart-head">
                  <h2>NAME OF EVENT</h2>
                  <p>
                    Akshya Nagar 1st Block 1st Cross, Rammurthy nagar,
                    Bangalore-560016
                    </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="cat-m">
                  <p>Sport</p>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12 catd">
                <Link>
                  <span>
                    <img src={iconLocation} alt="iconLocation" />
                  </span>
                  Get Directions
                  </Link>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="cart-main">
                  <div className="cartind">
                    <div className="ecn">
                      <div className="event-partcipant">
                        <h3>Category name</h3>
                        <div className="participant-info">
                          <p className="name">Ajay Ostawal</p>
                          <div className="p-details">
                            <p>Male</p>
                            <p>12.11.1990</p>
                          </div>
                          <Link>Hide participant details</Link>
                        </div>
                      </div>
                    </div>
                    <div className="ecd">
                      <div className="evtdt">
                        <p>17.02.2019</p>
                        <p>18:00</p>
                      </div>
                    </div>
                    <div className="ecp">
                      <div className="evtamount"> 5000</div>
                    </div>
                    <div className="eca">
                      <img src={deleteIcon} alt="deleteIcon" />
                    </div>
                  </div>
                  <hr className="chr" />
                  <div className="cartind">
                    <div className="ecn">
                      <div className="event-partcipant">
                        <h3>Category name</h3>
                        <div className="participant-info">
                          <p className="name">Ajay Ostawal</p>
                          <div className="p-details">
                            <p>Male</p>
                            <p>12.11.1990</p>
                          </div>
                          <Link>Hide participant details</Link>
                        </div>
                      </div>
                    </div>
                    <div className="ecd">
                      <div className="evtdt">
                        <p>17.02.2019</p>
                        <p>18:00</p>
                      </div>
                    </div>
                    <div className="ecp">
                      <div className="evtamount"> 5000</div>
                    </div>
                    <div className="eca">
                      <img src={deleteIcon} alt="deleteIcon" />
                    </div>
                  </div>
                </div>
                <div className="subtotal">
                  Total Cost: <span> 10000</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="bgWhiteShadow w-100 mtb40">
          <div className="coupon-sec">
            <label>Have A Coupon Code ?</label>
            <div className="row">
              <div className="col-md-5">
                <input type="text" className="form-control" />
              </div>
              <div className="col-md-2">
                <button className="btn btn-default" type="button">
                  Apply
                  </button>
              </div>
            </div>
            <div className="reward-point">
              <label>Use reward points</label>
              <h4>
                You have <span>53</span> reward points
                </h4>
              <div className="row mt25">
                <div className="col-md-5">
                  <div className="points-fld">
                    <p>How much you want to use ?</p>
                    <input type="text" className="form-control" />
                  </div>
                </div>
                <div className="col-md-2">
                  <button className="btn btn-default" type="button">
                    Use
                    </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="bgWhiteShadow w-100 mtb40">
          <div className="coupon-sec finaltotal">
            <div className="subtotal">
              Total Cost: <span> 10000</span>
            </div>
            <div className="ptp">
              <Link className="back-btn">
                <img src={arrowLeft} alt="arrowLeft" /> Back
                </Link>
              <Link className="ptp-btn order-firsti">
                Proceed to pay
                </Link>
              <Link className="ex-more">
                Explore more
                </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}