import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class AccountWrapper extends Component {
  render() {
    return (
      <section className="grey-bg">
        <div className="container">
          <div className="user-profile">
            <h5><Link to="/">Home </Link> <i className="fa fa-chevron-right"></i> My Account</h5>
            <h1>My account</h1>
            <div className="row">
              <div className="col-md-3">
                <div className="white-bg">
                  <ul className="list-user">
                    <li><Link to="user-profile.html">User profile</Link></li>
                    <li><Link className="current" to="fts-play-points.html">FTS Play Points<span><i className="fa fa-chevron-right"></i></span></Link></li>
                    <li><Link to="history.html">History</Link></li>
                    <li><Link to="#">Clubs</Link></li>
                    <li><Link to="#">Refer and earn</Link></li>
                    <li><Link to="#">Support</Link></li>
                    <li><Link to="#">Terms and Policies</Link></li>
                  </ul>
                </div>
              </div>
              <div className="col-md-9">
                {this.props.children}
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}


// AccountWrapper.defaultProps = {
//   active: false,
//   AcRight: console.warn("AcRight Element is not created"),
// };

export default AccountWrapper;