import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from './pages/Home';
import LogIn from './pages/LogIn';
import SignUp from './pages/SignUp';
import FTSPlayPoints from './pages/MyAccount/FTSPlayPoints';
import History from './pages/MyAccount/History';
import UserProfile from './pages/MyAccount/UserProfile';
import Cart from './pages/Events/Cart';
import Event from './pages/Events';
import EventDetailWrapper from './pages/Events/EventDetailWrapper';
import Registration from './pages/Events/Registration';
import {withRouter} from 'react-router'

class App extends Component {
  render() {
    return (
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/login" component={LogIn} />
        <Route path="/signup" component={SignUp} />
        {/* <Route path="/myaccount/:acpage" component={MyAccount} /> */}

        {/* MyAccount Page */}
        <Route path="/ftsplaypoints" component={FTSPlayPoints} />
        <Route path="/history" component={History} />
        <Route path="/userprofile" component={UserProfile} />

        {/* Events Page */}
        <Route path="/cart" component={Cart} />
        <Route path="/event" component={Event} />
        <Route path="/eventdetail" component={EventDetailWrapper} />
        <Route path="/registration" component={Registration} />
      </Switch>
    );
  }
}

export default withRouter(App);