class History extends Component {
  render() {
    return (
      <React.Fragment>
        <Header />
        <AccountWrapper>
          <HistoryDetail />
        </AccountWrapper>
        <Footer />
        <FooterBottom />
      </React.Fragment>
    );
  }
}

