import React, { Component } from 'react';
import Header from "components/Header";
import Footer from "components/Footer";
import FooterBottom from "components/Footer/FooterBottom";
import AccountWrapper from './AccountWrapper';

class UserProfile extends Component {
  render() {
    return (
      <React.Fragment>
        <Header />
        <AccountWrapper>
          <UserProfileComp />
        </AccountWrapper>
        <Footer />
        <FooterBottom />
      </React.Fragment>
    );
  }
}

const UserProfileComp = () => {
  return (
    <div className="white-bg user">
      <h3>User Profile</h3>
      <form>
        <h4>Profile photo</h4>
        <div className="row">
          <div className="col-md-8">
            <div>
              <label>Change the image</label>
            </div>
            <button>Choose image</button>
            <input
              type="file"
              className="hide_file"
            />
            <p>jpg, jpeg, png</p>
          </div>
          <div className="col-md-4">
            {/* <img
                id="user-photo"
                src='http://placehold.it/180'
                alt="your image"
              /> */}
          </div>
        </div>
        <h5>Main Information</h5>
        <hr />
        <div className="row">
          <div className="col-md-6">
            <label>Your Name</label>
            <div className="wrap-input50">
              <input className="input50" type="text" name="Your Name" />
            </div>
          </div>
          <div className="col-md-6">
            <label>Phone Number</label>
            <div className="wrap-input50">
              <input className="input50" type="tel" name="phone" />
            </div>
          </div>
          <div className="col-md-6">
            <label>Date of birth</label>
            <div className="wrap-input50">
              <div className="input-group input-append date" id="datePicker">
                <input
                  type="text"
                  className="form-control"
                  name="date"
                  placeholder="MM/DD/YYY"
                />
                <span className="input-group-addon add-on">
                  <i className="fa fa-calendar" aria-hidden="true" />
                </span>
              </div>
            </div>
          </div>
          <div className="col-md-6">
            <label>Email</label>
            <div className="wrap-input50">
              <input className="input50" type="text" name="email" />
            </div>
          </div>
          <div className="col-md-6">
            <label>Gender</label>
            <div className="gender">
              <label className="radio-inline">
                <input type="radio" name="Gender" defaultChecked /> Female
                </label>
              <label className="radio-inline">
                <input type="radio" name="Gender" /> Male
                </label>
              <label className="radio-inline">
                <input type="radio" name="Gender" /> Other
                </label>
            </div>
          </div>
        </div>
        <h5>Change the password</h5>
        <hr />
        <div className="row">
          <div className="col-md-6">
            <label>New password</label>
            <div className="wrap-input50">
              <input className="input50" type="text" name="Your Name" />
            </div>
          </div>
          <div className="col-md-6">
            <label>Retype new password</label>
            <div className="wrap-input50">
              <input className="input50" type="tel" name="phone" />
            </div>
          </div>
          <button
            className="sign-bt login100-form-btn btn-orange"
          >
            Save
            </button>
        </div>
      </form>
    </div>
  )
}

export default UserProfile;
